.current {
  color: rgb(255, 255, 255);
}

.vertical-center {
	margin-top: 0px;
	margin-bottom: 0px;
	margin-right: 0px;
	margin-left: 60px;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.header-image-bg {
	background-image: url("patterns/header-profile.png");
	min-height: 100px;
  margin-bottom: 0px;
  position: relative;
}

.header-container {
	margin-right: 50px;
	display: flex;
	height: 95px;
	text-align: center;
	float: right;
	width: 200px;
	vertical-align: middle;
	position: relative;
}

.header-text-cont {
	position: absolute;	
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
	width: max-content;
}

.link-header {
	color: #999c9e;
	font-size: 14px;
	font-weight: 600;
}

.navbar-side-panel {
  z-index: 2001;
  position: absolute;
  width: 220px;
  background-color: #10242d;
  margin-left: 20px;
  margin-right: 15px;
  margin-top: 15px
}

.img-symbol {
  width: 30px;
}

.img-git {
  width: 50px;
}

.grey-text {
  color: #999c9e;
}

.link-footer {
  float: right;
  margin-right: 15px;
}

.footer-mail {
  margin-top: 16px;
}

.dark-bg {
  background-color: transparent;
  margin-top: 15px;
}

.title {
  font-size: 35px;
  margin-bottom: 20px;
  color: white;
  font-weight: 400;
  line-height: 1.2;
  font-family: "Roboto", sans-serif;
}

.sub-title {
  padding: 130px 0 0px 0;
  margin-bottom: -100px;
  display: block;
  position: relative;
  top: -110px;
  z-index: -1;
  font-size: 26px;
  color: white;
  font-weight: 400;
  font-family: "Roboto", sans-serif;
}

.paragraph {
  margin-top: 0;
  margin-bottom: 1rem;
  color: white;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
  text-align: left;
}

.content-page {
  margin: 0 0 0 220px;
}

